@charset "UTF-8";
@font-face {
  font-family: "Tahoma";
  src: url("../fonts/Tahoma.woff2") format("woff2"), url("../fonts/Tahoma.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Tahoma", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff; }

body * {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.auth {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .auth h2 {
    margin: 0;
    font-size: 22px;
    font-weight: normal; }
  .auth__wrapper {
    width: 500px; }
  .auth__tab-wrapper {
    display: flex; }
  .auth__tab {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 7px 15px;
    text-align: center;
    background-color: #fdfdfd;
    border: 1px solid #eeeeee; }
    .auth__tab--travel {
      width: 100%; }
    .auth__tab--active {
      color: #fdfdfd;
      background-color: #102d6d;
      border: 1px solid #102d6d;
      outline: 1px solid #102d6d;
      pointer-events: none; }
  .auth__tab:hover {
    color: #ffffff;
    background-color: #102d6d;
    border: 1px solid #102d6d;
    cursor: pointer; }
  .auth__form-wrapper--hidden {
    display: none; }
  .auth__form-wrapper--show {
    display: block; }
  .auth__form {
    width: 500px;
    padding-top: 15px;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 20px;
    background-color: #fdfdfd;
    border: 1px solid #eeeeee;
    border-top: 3px solid #da251c; }
  .auth__login-social {
    display: none; }

@media (max-width: 500px) {
  .auth__wrapper {
    padding: 0 10px;
    width: 100%; }
  .auth__form {
    width: 100%; } }

@media (max-width: 400px) {
  .auth > p {
    padding: 0 20px;
    text-align: center; } }

@media (max-width: 400px) {
  .auth__tab {
    width: 100%; } }

.form__header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 10px; }

.form__header-wrapper p {
  margin: 0;
  padding-bottom: 8px;
  color: #0c2969; }

.form__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 0 30px 0;
  padding: 0;
  list-style: none; }

.form__item {
  margin-bottom: 15px; }
  .form__item--currency {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-bottom: 15px; }
    .form__item--currency p {
      padding-bottom: 3px;
      margin: 0 25px 0 0;
      font-size: 14px; }
    .form__item--currency label {
      margin: 0 15px 0 5px;
      font-size: 14px;
      text-transform: uppercase; }
    .form__item--currency label:last-of-type {
      margin: 0 0 0 5px; }
  .form__item--terms {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-bottom: 15px; }
    .form__item--terms label {
      padding-bottom: 3px;
      margin: 0 0 0 10px; }
      .form__item--terms label a {
        text-decoration: underline;
        font-size: 14px; }
  .form__item label {
    display: block;
    margin-top: 5px;
    margin-bottom: 10px;
    padding-left: 10px;
    font-size: 14px; }
  .form__item input[type="radio"] {
    margin: 0;
    width: 15px; }
  .form__item input[type="checkbox"] {
    margin: 0;
    width: 15px; }
  .form__item--captcha {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 0; }
  .form__item input {
    width: 100%;
    padding: 7px 0 7px 10px;
    border: 1px solid #eeeeee; }
  .form__item input:hover,
  .form__item input:focus {
    border: 1px solid #102d6d; }

.form__error {
  padding-left: 10px;
  font-size: 13px;
  color: red; }
  .form__error--hidden {
    display: none; }

.form__submit-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end; }

.form__submit-wrapper a {
  padding-bottom: 8px;
  font-size: 12px;
  text-decoration: underline; }

.form__submit {
  padding: 8px 19px;
  margin-left: 15px;
  color: #fdfdfd;
  font-size: 16px;
  border: none;
  background-color: #102d6d;
  cursor: pointer; }

.form__captcha {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
  width: 100%;
  margin-right: 20px; }

.form__captcha p {
  display: inline-block;
  color: #0c2969; }

.form__captcha input {
  width: 100%;
  padding: 7px 0 7px 10px; }

@media (max-width: 450px) {
  .form__item:last-of-type {
    flex-direction: column;
    justify-content: space-between; }
  .form__captcha {
    margin-bottom: 10px; }
  .form__item input {
    padding: 15px 0 15px 10px; }
  .form__captcha input {
    padding: 15px 0 15px 10px; } }

@media (max-width: 380px) {
  .form__submit {
    margin-left: auto; }
  .form__item--terms label {
    margin: 0 0 0 15px; }
    .form__item--terms label a {
      text-decoration: underline;
      font-size: 14px; } }

@media (max-width: 340px) {
  .form__item--currency p {
    margin: 0 9px 0 0; } }

